import styled from 'styled-components';
import BackgroundImage from 'gatsby-background-image'
import {device} from '../../../theme'
import theme from '../../../theme'

// import BannerShape from '../../../assets/img/home-one-banner-shape.svg'

const buffer = Buffer.from(`<svg xmlns="http://www.w3.org/2000/svg" width="1920" height="459"><path style="fill:${theme.colors.bgColor}; stroke:none;" d="M0 20L0 454C640.025 454 1279.97 459 1920 459L1920 293L1920 244C1920 237.464 1921.68 228.178 1919.4 222C1916.86 215.129 1907.43 209.773 1902 205.247C1887 192.745 1871.63 180.83 1856 169.127C1790.1 119.784 1716.1 79.6939 1639 50.9499C1558.84 21.0652 1474.4 3.77948 1389 0.0393677C1277.33 -4.85086 1163.77 15.7672 1059 54.0532C1027.9 65.4191 996.821 77.8295 967 92.2585C915.304 117.272 865.563 145.917 813 169.14C714.128 212.822 609.075 240.228 501 244.961C486.963 245.575 472.968 244.651 459 244.039C334.336 238.58 215.928 196.475 115 122.849C88.2661 103.347 62.9044 81.8182 39 58.9606C31.2175 51.519 23.429 43.879 16.0895 36C10.8994 30.4283 6.27412 24.4196 0 20z"/></svg>`).toString('base64');
const updatedBannerShape = `data:image/svg+xml;base64,${buffer}`;

export const HeroWrapper = styled.section `
    height: 500px;
    position: relative;
    ${device.xsmall}{
        height: 650px;
    }
    ${device.medium}{
        height: 750px;
    }
    ${device.large}{
        height: 850px;
    }
    ${device.xlarge}{
        height: 1080px;
    }
    &:before{
        position: absolute;
        content: '';
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
        background-color: rgb( 0, 0, 0, .2 );
    }
    &:after{
        position: absolute;
        content: '';
        left: 0;
        bottom: -5px;
        z-index: 1;
        width: 100%;
        height: 100px;
        background: url(${updatedBannerShape}) no-repeat scroll 0 0;
        background-size: 100% 100%;
        ${device.xsmall}{
            height: 160px;
        }
        ${device.small}{
            height: 200px;
        }
        ${device.medium}{
            height: 240px;
        }
        ${device.large}{
            height: 280px;
        }
    }
`;

export const HeroBG = styled(BackgroundImage) `
    position: absolute !important;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
`;

export const HeroTextBox = styled.div `
    padding-top: 200px;
    ${device.xsmall}{
        padding-top: 210px;
    }
    ${device.medium}{
        padding-top: 230px;
    }
    ${device.large}{
        padding-top: 268px;
    }
    * {
        position: relative;
        z-index: 9;
    }
`; 