import React from 'react';
import { FiChevronLeft, FiChevronRight } from "react-icons/fi";
import { PaginationWrap, PaginationList, PaginationItem, PaginationLink } from './pagination.stc'


function pages(currentPage, numberOfPages, maxPages) {
    const right = Math.floor(maxPages/2)
    const left = maxPages - right - 1
    var start = currentPage - left > 0 ? currentPage - left : 1
    if (start === 1) {
        return Array.from({ length: numberOfPages }, (_, i) => i + 1).filter(v => v <= maxPages)
    }

    var end = currentPage + right <= numberOfPages ? currentPage + right : numberOfPages
    if (end === numberOfPages) {
        return Array.from({ length: numberOfPages }, (_, i) => i + 1).filter(v => v > numberOfPages - maxPages)
    }

    return Array.from({ length: numberOfPages }, (_, i) => i + 1).filter(v => v >= start && v <= end)
}

const Pagination = ({ currentPage, numberOfPages, rootPage }) => {
    const isFirst = currentPage === 1;
    const isLast = currentPage === numberOfPages;
    const previousPage = currentPage - 1 === 1 ? rootPage : '/page/' + (currentPage - 1).toString();
    const nextPage = '/page/' + (currentPage + 1).toString();

    const maxPages = 7

    return (
        <PaginationWrap>
            <PaginationList>
                {isFirst ? (
                    <PaginationItem disabled={true}>
                        <PaginationLink className="prev" path={previousPage}>
                            <FiChevronLeft />
                            <span>Prev</span>
                        </PaginationLink>
                    </PaginationItem>
                ) : (
                        <PaginationItem>
                            <PaginationLink className="prev" path={previousPage}>
                                <FiChevronLeft />
                                <span>Prev</span>
                            </PaginationLink>
                        </PaginationItem>
                    )}
                {Array.from(pages(currentPage, numberOfPages, maxPages), (i) => currentPage === i ? (
                    <PaginationItem key={`page-number-${i}`} id={`page-number-${i}`}>
                        <PaginationLink active="true" path={`${i === 1 ? '' : '/page/' + (i)}`}>
                            {i}
                        </PaginationLink>
                    </PaginationItem>
                ) : (
                        <PaginationItem key={`page-number-${i + 1}`} id={`page-number-${i}`}>
                            <PaginationLink path={`${i === 1 ? rootPage : '/page/' + (i)}`}>
                                {i}
                            </PaginationLink>
                        </PaginationItem>
                    ))}
                {isLast ? (
                    <PaginationItem disabled={true}>
                        <PaginationLink className="next" path={nextPage}>
                            <span>Next</span>
                            <FiChevronRight />
                        </PaginationLink>
                    </PaginationItem>
                ) : (
                        <PaginationItem>
                            <PaginationLink className="next" path={nextPage}>
                                <span>Next</span>
                                <FiChevronRight />
                            </PaginationLink>
                        </PaginationItem>
                    )}
            </PaginationList>
        </PaginationWrap>
    )
}

export default Pagination;